.analyticsDashboard {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
}

.analyticsDashboard__title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
}

.analyticsDashboard__value {
    font-family: "Inter";
    font-weight: 600;
    font-size: 30px;
    line-height: 1;
    color: #fff;
    padding-top: 10px;
}